.login-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
}

.login-container {
  background: white;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
  overflow: hidden;
}

.login-header {
  text-align: center;
  padding: 40px 30px 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.login-header h1 {
  margin: 0 0 10px 0;
  font-size: 2rem;
  font-weight: bold;
}

.login-header p {
  margin: 0;
  opacity: 0.9;
  font-size: 1rem;
}

.login-content {
  padding: 40px 30px;
}

.login-loading {
  text-align: center;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #667eea;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.login-loading p {
  color: #666;
  margin: 0;
}

.login-error {
  text-align: center;
}

.error-icon {
  font-size: 3rem;
  margin-bottom: 20px;
}

.login-error h3 {
  color: #e74c3c;
  margin: 0 0 10px 0;
}

.login-error p {
  color: #666;
  margin: 0 0 20px 0;
}

.retry-btn {
  background: #e74c3c;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.3s ease;
}

.retry-btn:hover {
  background: #c0392b;
}

.login-instructions {
  text-align: center;
}

.instruction-icon {
  font-size: 3rem;
  margin-bottom: 20px;
}

.login-instructions h3 {
  color: #333;
  margin: 0 0 15px 0;
}

.login-instructions p {
  color: #666;
  line-height: 1.6;
  margin: 0 0 25px 0;
}

.login-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  width: 100%;
}

.login-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.3);
}

.login-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.telegram-warning {
  margin-top: 20px;
  padding: 15px;
  background: #fff3cd;
  border: 1px solid #ffeaa7;
  border-radius: 8px;
}

.telegram-warning p {
  color: #856404;
  margin: 0;
  font-size: 0.9rem;
}

.login-footer {
  text-align: center;
  padding: 20px 30px 30px;
  background: #f8f9fa;
  border-top: 1px solid #eee;
}

.login-footer p {
  color: #666;
  margin: 0;
  font-size: 0.9rem;
}
