.navigation {
  background: white;
  border-top: 1px solid #e1e5e9;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.1);
}

.nav-content {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 8px 0;
  max-width: 1200px;
  margin: 0 auto;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px 12px;
  border: none;
  background: none;
  cursor: pointer;
  transition: all 0.2s ease;
  border-radius: 12px;
  min-width: 60px;
  text-decoration: none;
  color: #666;
}

.nav-item:hover {
  background: #f8f9fa;
  color: #333;
}

.nav-item.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.nav-item.active:hover {
  background: linear-gradient(135deg, #5a67d8 0%, #6b46c1 100%);
}

.nav-icon {
  font-size: 1.5rem;
  margin-bottom: 4px;
  display: block;
}

.nav-label {
  font-size: 0.75rem;
  font-weight: 500;
  text-align: center;
  line-height: 1.2;
}

/* Mobile responsive adjustments */
@media (max-width: 768px) {
  .nav-content {
    padding: 6px 0;
  }

  .nav-item {
    padding: 6px 8px;
    min-width: 50px;
  }

  .nav-icon {
    font-size: 1.3rem;
    margin-bottom: 2px;
  }

  .nav-label {
    font-size: 0.7rem;
  }
}

/* For very small screens */
@media (max-width: 480px) {
  .nav-item {
    padding: 4px 6px;
    min-width: 45px;
  }

  .nav-icon {
    font-size: 1.2rem;
  }

  .nav-label {
    font-size: 0.65rem;
  }
}
